<template>
  <div class="ling-login">
    <el-row class="login-top">
      <el-col :xs="24" :sm="12" :md="6">
        <img src="~@/assets/logo.png" />
      </el-col>
    </el-row>
    <el-row class="login-main">
      <el-col>
        <div class="title">欢迎使用小诺机器人安卓配置系统</div>
        <el-form
          :model="ruleFrom"
          :rules="rules"
          ref="ruleFrom"
          label-position="left"
          label-width="0px"
          class="login-container"
          @keyup.enter.native.prevent="submitForm('ruleFrom')"
        >
          <el-tabs v-model="activeName">
            <!-- 账户密码登录 -->
            <el-tab-pane label="账户密码登录" name="first">
              <el-form-item prop="username">
                <el-input
                  type="text"
                  v-model="ruleFrom.username"
                  auto-complete="off"
                  placeholder="用户名"
                  prefix-icon="menu-icon el-icon-user-solid"
                ></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input
                  type="password"
                  v-model="ruleFrom.password"
                  auto-complete="off"
                  placeholder="密码"
                  prefix-icon="menu-icon el-icon-lock"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  type="primary"
                  @click.native.prevent="submitForm('ruleFrom')"
                  v-loading="logining"
                >登录</el-button>
              </el-form-item>
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('login')
export default {
  name: 'LingLogin',
  data () {
    return {
      logining: false,
      activeName: 'first',
      ruleFrom: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    ...mapActions(['login']),
    /**
     * @description: 用户登录
     * @param {String} 登录表单名称
     */
    submitForm (formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          this.logining = true
          let formData = {
            username: this.ruleFrom.username,
            password: this.ruleFrom.password
          }
          try {
            await this.login(formData)
            this.$router.push({
              path: '/home'
            })
          } catch (e) {
            this.logining = false
            this.$message.error(e.message)
          }
        }
      })
    }
  }
}
</script>

<style lang="scss">
.ling-login {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to bottom, #e6f3ff, #b9e3fb);
  .login-top {
    padding-top: 30px;
    height: 186px;
    .el-col {
      text-align: center;
    }
  }
  .login-main {
    max-width: 512px;
    margin: -50px auto;
    border-radius: 10px;
    .title {
      margin-bottom: 80px;
      font-size: 28px;
      text-align: center;
      color: #2189e0;
    }
  }
  .login-container {
    max-width: 350px;
    padding: 30px 80px;
    margin: 0 auto;
    border-radius: 5px;
    background: #fff;
    box-shadow: 3px 2px 2px 3px #fff;
    .el-button--primary {
      width: 100%;
    }
  }

  @media screen and (max-width: 800px) {
    .login-main {
      border-radius: 5px;
      .title {
        margin-bottom: 60px;
        font-size: 22px;
      }
    }
    .login-container {
      padding: 10px 20px;
    }
  }

  @media screen and (max-width: 600px) {
    .login-container {
      max-width: 80%;
      padding: 5% 5%;
    }
  }
}
</style>



